 * issues: https://github.com/mescroll/mescroll/issues
 * QQ交流群: 633126761
 * 
 * 温馨提示: mescroll唯一的全局样式: html,body{height:100%},用于固定body的高度满屏; 如果影响到您原本的项目样式,可自行删除
 */
/*设置body的高度,以便正确的获取body高度和触发scroll事件*/
html,
body {
	height: 100%;
}
/*使iOS列表滑动流畅*/
body {
	-webkit-overflow-scrolling: touch;
}
/*下拉刷新和上拉加载的滑动区域*/
.mescroll {
	width: 100%;
	height: 100%;
	overflow-y: auto;
}
/*启用硬件加速:使动画渲染流畅,解决部分手机闪白屏问题,在下拉刷新和上拉加载触发时启用,结束后移除,避免滥用导致其他兼容性问题*/
.mescroll-hardware{
    -webkit-transform:translateZ(0);
    -webkit-transform-style:preserve-3d;
    -webkit-backface-visibility:hidden;
    -webkit-perspective:1000
}
/*下拉刷新区域*/
.mescroll-downwarp{
    position:relative;
    width:100%;height:0;
    overflow:hidden;
    text-align:center
}
/*下拉刷新--高度重置的过渡动画*/
.mescroll-downwarp-reset{
    -webkit-transition:height 300ms;
    transition:height 300ms
}
/*下拉刷新--内容区,定位于区域底部*/
.mescroll-downwarp .downwarp-content{
    position:absolute;left:0;bottom:0;width:100%;
    min-height:18px;padding:10px 0
}
.mescroll-upwarp{
    min-height:18px;
    padding:15px 0;
    text-align:center;
    visibility:hidden;/*代替display: none,列表快速滑动到底部能及时显示上拉加载的区域*/
}
/*下拉刷新,上拉加载--提示文本*/
.mescroll-downwarp .downwarp-tip,.mescroll-upwarp .upwarp-tip,.mescroll-upwarp .upwarp-nodata{
    display:inline-block;
    font-size:12px;
    color:gray;vertical-align:middle;
}
/*下拉刷新,上拉加载--旋转进度条*/
.mescroll-downwarp .downwarp-progress,.mescroll-upwarp .upwarp-progress{
    display:inline-block;
    width:16px;height:16px;
    border-radius:50%;
    border:1px solid gray;
    margin-right:8px;
    border-bottom-color:transparent;
    vertical-align:middle
}
/*旋转动画*/
.mescroll-rotate{
    -webkit-animation:mescrollRotate .6s linear infinite;
    animation:mescrollRotate .6s linear infinite
}
@-webkit-keyframes mescrollRotate{
0%{-webkit-transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg)
}
}
@keyframes mescrollRotate{
0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)
}
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
