}
.mescroll-empty .empty-btn:active{opacity:.75
}
/*回到顶部的按钮*/
.mescroll-totop{
    z-index:9990;
    position:fixed;
    right:10px;bottom:30px;
    width:36px;height:36px;
    border-radius:50%;opacity:0
}
/*显示动画--淡入*/
.mescroll-fade-in{
    -webkit-animation:mescrollFadeIn .5s linear forwards;
    animation:mescrollFadeIn .5s linear forwards
}
@-webkit-keyframes mescrollFadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
@keyframes mescrollFadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
/*隐藏动画--淡出*/
.mescroll-fade-out{
    pointer-events:none;
    -webkit-animation:mescrollFadeOut .5s linear forwards;
    animation:mescrollFadeOut .5s linear forwards
}
@-webkit-keyframes mescrollFadeOut{
0%{opacity:1
}
100%{opacity:0
}
}
@keyframes mescrollFadeOut{
0%{opacity:1
}
100%{opacity:0
}
}
